import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { AiOutlineRight } from 'react-icons/ai';
import { useNavigate } from 'react-router-dom';
import { PersonApi } from 'utils/webApi';

const Personal = () => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [currentUser, setCurrentUser] = useState(null);
  const [imageUrl, setImageUrl] = useState(null);
  const [avatarUrl, setAvatarUrl] = useState(null);

  useEffect(() => {
    PersonApi().then((res) => {
      setCurrentUser(res.data.currentUser);
			setImageUrl(res.data.centerData.imageUrl)
			setAvatarUrl(res.data.centerData.avatarUrl)
    }, []);
  }, []);

  const presonlList = [
    {
      icon: '',
      info: t('my_team'),
      path: '/team',
    },
    {
      icon: '',
      info: t('my_wallet'),
      path: '/wallet',
    },
    {
      icon: '',
      info: t('platform_post'),
      path: '/post',
    },
    {
      icon: '',
      info: t('about_us'),
      path: '/about',
    },
    {
      icon: '',
      info: t('app_download'),
      path: '',
    },
    {
      icon: '',
      info: t('logout_sys'),
      path: '/login',
    },
  ];
  return (
    <div className="flex flex-col gap-4">
      <div className="flex justify-between items-center m-2 mt-8">
        <div className="flex flex-col   gap-4">
          <p className="text-xl font-bold">
            {currentUser ? currentUser.phone : 'xxxxxxxxxxxxxx'}
          </p>
          <p className="text-xs text-gray-500 ">
            {t('person_nu')}ï¼š
            {currentUser ? currentUser._id.slice(0, 7) : 'xxxxxxxxxxxxxxxx'}
          </p>
        </div>
        <img
          src={process.env.REACT_APP_SERVER + avatarUrl}
          alt="logo"
          className="ml-auto"
        />
      </div>
        <img className="w-screen h-24 rounded-3xl" src={process.env.REACT_APP_SERVER + imageUrl} alt="post Img" />
      <ul className=""></ul>
      {presonlList.map((item, index) => (
        <li
          key={index}
          className="flex justify-start items-center ml-2 mr-2 text-sm gap-1 "
          onClick={() => navigate(item.path)}
        >
          <img src={item.icon} alt="" />
          <p className="">{item.info}</p>
          <AiOutlineRight className="ml-auto" />
        </li>
      ))}
    </div>
  );
};

export default Personal;
